    transform: translateX(-50%);
}
#creature-inspector .gene .label {
    text-align: center;
    margin: 0;
}
#creature-inspector .gene .slider {
    width: 80%;
    height: 1.5rem;
    margin-left: 50%;
    border: 2px solid #ccc;
    transform: translateX(-50%);
}
#creature-inspector .gene .slider .slider-fill {
    height: 1.5rem;
}

#creature-inspector .gene .min {
    position: absolute;
    left: 0;
}
#creature-inspector .gene .val {
    background-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    left: 50%;
    transform: translate(-50%, -100%);
}
#creature-inspector .gene .max {
    position: absolute;
    right: 0;
}
#graph-viewer {
   width: clamp(50%, 1000px, 90%);
   max-height: 80%;
   background-color: #28282b;
   border-radius: 10px;
   padding: 10px;
   position: absolute;
   top: 10%;
   left: 50%;
   transform: translateX(-50%);
   z-index: 5;
   box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.5);
   overflow-y: scroll;
}

#graph-viewer > .formatter {
   width: 100%;
   display: flex;
}
#graph-viewer > .formatter > * {
   flex-grow: 0;
   flex-shrink: 0;
   flex-basis: 50%;
}

#graph-viewer .option {
   color: #ddd;
   margin-bottom: 3px;
   cursor: pointer;
}
#graph-viewer .option .description {
   font-style: italic;
   font-size: 0.9rem;
   line-height: 0.9rem;
   color: rgb(171, 171, 189);
   margin: 1px 0 10px 20px;
}

#graph-viewer .graph-container {
   width: 100%;
   margin: 10px 0 30px;
   display: flex;
   flex-wrap: wrap;
   justify-content: space-evenly;
}

#graph-viewer .graph-container p {
   max-width: 60%;
   color: #878799;
   text-align: center;
   font-weight: bold;
   font-style: italic;
   margin-left: 50%;
   display: inline-block;
   transform: translateX(-50%);
}
.graph {
   --point-size: 2px;

   border: 1px solid #000;
   margin: 20px 0 40px;
   position: relative;
}

.graph .title {
   font: 600 0.95rem "Source Code Pro";
   position: absolute;
   left: 50%;
   top: -2px;
   transform: translate(-50%, -100%);
   white-space: pre;
}

.graph .point {
   width: var(--point-size);
   height: var(--point-size);
   position: absolute;
   z-index: 3;
}

.graph .line {
   height: 1px;
   position: absolute;
   transform-origin: top left;
   z-index: 3;
}

.graph .rect {
   position: absolute;
   z-index: 2;
}

.graph .triangle {
   width: 0;
   height: 0;
   position: absolute;
   z-index: 2;
}
.graph .triangle.top-left {
   border-top: var(--width) solid var(--colour);
   border-right: var(--height) solid transparent;
   border-bottom: var(--width) solid transparent;
   border-left: var(--height) solid var(--colour);
}
.graph .triangle.top-right {
   border-top: var(--width) solid var(--colour);
   border-right: var(--height) solid var(--colour);
   border-bottom: var(--width) solid transparent;
   border-left: var(--height) solid transparent;
}
.graph .triangle.bottom-right {
   border-bottom: var(--width) solid var(--colour);
   border-right: var(--height) solid var(--colour);
   border-left: var(--height) solid transparent;
   border-top: var(--width) solid transparent;
}
.graph .triangle.bottom-left {
   border-bottom: var(--width) solid var(--colour);
   border-left: var(--height) solid var(--colour);
   border-top: var(--width) solid transparent;
   border-right: var(--height) solid transparent;
}

.graph .measurement {
   font: 400 0.85rem "Open Sans 3";
   color: #ccc;
   position: absolute;
}
.graph .measurement.y {
   transform: translate(-100%, 50%);
}
.graph .measurement.x {
   bottom: 2px;
   transform: translate(-50%, 100%);
}

.graph .label {
   color: rgb(212, 211, 226);
   font: 600 1rem "Source Sans 3";
   font-style: italic;
   font-weight: bold;
   position: absolute;
}
.graph .x-label {
   left: 50%;
   bottom: -10px;
   transform: translate(-50%, 100%);
}
.graph .y-label {
   top: 50%;
   left: -30px;
   transform: translateX(-50%) rotate(-90deg);
}
#pause-screen {
   background-color: rgba(24, 23, 43, 0.5);
   position: absolute;
   top: 0;
   right: 0;
   bottom: 0;
   left: 0;
   z-index: 6;
}

#pause-screen hgroup {
   background-color: #28282b;
   padding: 0.5rem 1rem;
   border: 2px solid #000;
   border-radius: 10px;
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   box-shadow: 3px 3px 8px 2px rgba(0, 0, 0, 0.6);
}
#pause-screen h1 {
